html {
  font-weight: 300;
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #404e88;
  color: #f1f3f8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a,
a:hover,
a:visited {
  color: #f1f3f8;
}

.header {
  grid-area: header;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.header-name {
  display: -webkit-flex;
  display: flex;
}

.header-title {
  display: -webkit-flex;
  display: flex;
}

.header-contact {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.header-contact div {
  display: -webkit-flex;
  display: flex;
}

.header-contact div a {
  padding: 0 1rem;
}

.link-button {
  padding: 0 1rem;
  text-decoration: underline;
  color: #f7f7f8;
  border: none;
  background-color: transparent;
}

.blurb {
  grid-area: blurb;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.blurb-heading {
  display: -webkit-flex;
  display: flex;
}

.blurb-text {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  line-height: 1.35rem;
}

.learn-next {
  grid-area: learn-next;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  align-self: flex-start;
}
.learn-next-title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.learn-next-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.learn-next-list li {
  list-style-type: none;
  padding: 1rem;
}

.open-source {
  grid-area: open-source;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.open-source-title {
  display: -webkit-flex;
  display: flex;
}

.open-source-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.open-source-list li {
  list-style-type: none;
  margin: 0.5rem;
}

.programming {
  grid-area: programming;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.programming-title {
  display: -webkit-flex;
  display: flex;
  line-height: 2rem;
}

.programming-list {
  display: -webkit-flex;
  display: flex;
  padding: 0;
}

.programming-language {
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

.programming-list ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.programming-list ul li {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  list-style-type: none;
}

.side-projects {
  grid-area: side-projects;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.side-projects-title {
  display: -webkit-flex;
  display: flex;
}

.side-projects-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0;
}

.side-projects-list ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  text-align: right;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding: 0;
}

.side-projects-list li {
  display: -webkit-flex;
  display: flex;
  list-style-type: none;
  line-height: 2rem;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.systems {
  grid-area: systems;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.systems-title {
  display: -webkit-flex;
  display: flex;
}

.systems-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.systems-platform {
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.systems-list ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.systems-list > ul > li {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex-direction: column;
          flex-direction: column;
  list-style-type: none;
  margin: 0.15rem;
}

.vendor-tech {
  grid-area: vendor-tech;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 0.85rem;
}

.vendor-tech-title {
  display: -webkit-flex;
  display: flex;
}

.vendor-tech-list {
  display: -webkit-flex;
  display: flex;
  padding: 0;
}

.vendor-name {
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

.vendor-tech-list ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.vendor-tech-list ul li {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  list-style-type: none;
}

.work-history {
  grid-area: work-history;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.work-history-title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.work-history-company {
  display: -webkit-flex;
  display: flex;
}

.work-history-item {
  display: -webkit-flex;
  display: flex;
}

.work-history-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.work-history-position {
  display: grid;
  grid-gap: 1rem;
  margin: auto;
  grid-template-areas:
    "title ..."
    "dates ..."
    "location ..."
    "description description"
    "... ...";
}

.work-history-position-title {
  display: -webkit-flex;
  display: flex;
  grid-area: title;
  /* border: 1px solid white; */
}

.work-history-position-title > h4 {
  padding: 0;
  margin: 0;
}

.work-history-dates {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  grid-area: dates;
}

.work-history-location {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  font-style: italic;
  grid-area: location;
}

.work-history-description {
  grid-area: description;
  line-height: 1.2rem;
}

.App {
  display: grid;
  min-height: 200vh;
  padding: 2rem;
  margin: 0 5rem;
  grid-template-areas:
    "... header header ..."
    "... blurb blurb ..."
    "vendor-tech work-history work-history side-projects"
    "open-source work-history work-history programming"
    "systems work-history work-history learn-next";
  grid-gap: 2rem;
}

.App > * {
  padding: 1rem;
  border: 0.5px #d0edf1 double;
  background-color: #181e34;
  box-shadow: 1px 8px 8px 0px rgba(0, 0, 0, 0.84);
}

